import React, { Component } from "react";
import "./index.css";

import axios from "axios";

import * as echarts from "echarts";

class EchartPage extends Component {
  render() {
    return <div id="main"></div>;
  }
  componentDidMount() {
    let chartDom = document.getElementById("main");
    let myChart = echarts.init(chartDom);
    let option;

    option = {
      xAxis: {
        type: "category",
        boundaryGap: false,
        data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          data: [820, 932, 901, 934, 1290, 1330, 1320],
          type: "line",
          areaStyle: {},
        },
      ],
    };

    option && myChart.setOption(option);
    this.http();
    this.http_2();
    this.http_3();
  }
	http_2() {
		this.props.http.$get('/websites/user/get',{id:3}).then((res => {
			console.log(res)
		}))
	}
  http_3() {
		this.props.http.$post('/websites/people/set',{
      name:'测试2',
      sex:1,
      address:'22222'
    }).then((res => {
			console.log(res)
		}))
	}
  http() {
    axios({
      url: "http://127.0.0.1:9000/setting/callCenterGray",
      method: "GET",
    })
      .then((res) => {})
      .catch((err) => {});
  }
}
export default EchartPage;
